}
}

.nav{
	position: fixed;
	left: 0;
	top: 0;
	z-index: 10;
	width: 150px;
	height: 100vh;
	background-color: #5d7092;
	-webkit-box-shadow: var(--box-shadow1);
	        box-shadow: var(--box-shadow1);
	-webkit-transition: var(--transition-speed);
	transition: var(--transition-speed);
}
.nav.hidden{
	-webkit-transform: translateX(-100%);
	        transform: translateX(-100%);
}
.nav .link{
	margin: 2px 0;
	padding: 15px 0;
	color: #F4F4F4;
	display: block;
	text-align: center;
	border-left: 3px solid transparent;
}
.nav .link:hover{
	background-color: rgba(0,0,0,0.3);
}
.nav .link.active{
	background-color: rgba(0,0,0,0.3);
	border-color: var(--green1);
}

.login{
	width: 100%;
	height: 100vh;
	background-color: #F4F4F4;
}
.login .box{
	position: absolute;
	left: 50%;
	-webkit-transform: translateX(-50%);
	        transform: translateX(-50%);
	top: 30%;
	width: 300px;
	background-color: #FFFFFF;
	border-radius: 10px;
	-webkit-box-shadow: var(--box-shadow1);
	        box-shadow: var(--box-shadow1);
	padding: 10px;
}
.login .box input{
	width: 100%;
	padding: 5px 10px;
	margin-bottom: 10px;
}
.login .box .btn{
	width: 50%;
	margin-left: 25%;
}

.home{
	margin-left: 150px;
	height: 100vh;
	background-color: #F4F4F4;
	padding: 10px;
}
.home .card{
	margin: 0 auto 15px auto;
	padding: 5px;
	background-color: #FFFFFF;
	border-radius: 5px;
	-webkit-box-shadow: var(--box-shadow1);
	        box-shadow: var(--box-shadow1);
}
.home .card h4{
	padding: 5px;
	border-bottom: var(--border2);
}
.home .card .title{
	width: 100%;
	border-bottom: var(--border2);
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-align: center;
	    -ms-flex-align: center;
	        align-items: center;
}
.home .card .title .btn{
	margin-left: 15px;
	height: 25px;
}
.home header .date{
	margin-left: 10px;
	color: var(--green2);
}
.home .count{
	margin-top: 5px;
	display: grid;
	grid-template-columns: repeat(5,1fr);
	grid-gap: 10px;
}
.home .count .item{
	padding: 10px;
	text-align: center;
	background-color: #F4F4F4;
	border-radius: 5px;
	position: relative;
}
.home .count .item p{
	margin: 5px 0;
	cursor: default;
}
.home .count .item .amount{
	font-size: 1.6em;
	font-weight: 500;
	color: var(--green2);
}
.home .input{
	margin: 10px 0;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
}
.home .input p{
	width: 80px;
}
.home .input input{
	padding: 0 5px;
	-webkit-box-flex: 1;
	    -ms-flex: 1;
	        flex: 1;
	max-width: 400px;
}
.home .chart{
	margin-top: 5px;
	display: grid;
	grid-template-columns: repeat(2,1fr);
	grid-gap: 10px;
}
@media (max-width:1000px) {
.home .count{
		grid-template-columns: repeat(3,1fr);
}
.home .chart{
		grid-template-columns: repeat(1,1fr);
}
}

